:root {
  --morning: #e8817f;
  --evening: #311f62;
}

body {
  padding-left: env(safe-area-inset-left);
  padding-right: env(safe-area-inset-right);
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
  position: relative;
}

.App-header .login {
  position: absolute;
  right: 50px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

.App-header .login button {
  padding: 0;
  background: none;
  color: white;
  border: none;
}

.App-title {
  font-size: 1.5em;
}

.date {
  font-size: 30px;
  margin-top: 20px;
}

.form-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 25px;
  width: 500px;
  max-width: 90%;
  padding: 50px 0;
}

.form-section--morning {
  background: var(--morning);
  color: #efefef;
}

.form-section--morning button[type="button"] {
  background: none;
  border: 2px solid #101010;
  margin-top: 15px;
  color: #101010;
}

.form-section--evening {
  background: var(--evening);
  color: #010101;
}

.form-section--evening button[type="button"] {
  background: none;
  border: 2px solid #efefef;
  margin-top: 15px;
  color: #efefef;
}


form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

button {
  padding: 20px 35px;
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
}

button[type="submit"] {
  background: rgba(114, 140, 20, .5);
  border: 2px solid #728C14;
  margin-top: 30px;
}

button[type="button"] {
  margin-top: 15px;
}


